body {
	font-family: sans-serif;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100vw;
	background: linear-gradient(to bottom, #2c5364, #203a43, #0f2027);
	color: rgb(217, 232, 241);
	font-family: "Inter", sans-serif;
	overflow: hidden;
}

input {
	font-family: Consolas, sans-serif;
	font-size: 28px;
	width: 6ch;
	padding: 5px;
	background-color: rgb(246, 244, 244);
	outline: none;
	border-radius: 10px;
	text-transform: uppercase;
}

main {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	background: radial-gradient(#1b2238, #303356ce);
	padding: 20px;
	border-radius: 20px;
	box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
		rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
		rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
	width: 300px;
	height: 400px;
}
#st-cont {
	display: flex;
	gap: 20px;
}

p {
	min-height: 30px;
	max-height: 30px;
}
#wordscore {
	font-size: 14px;
}
#result {
	font-family: Consolas, sans-serif;
}
#selected-chars {
	display: flex;
}
span {
	background-color: white;
	padding: 7px;
	border-radius: 5px;
	color: black;
	width: 30px;
	height: 30px;
}

.red {
	color: red;
}
.green {
	color: green;
}
